.main-wrapper { padding: 0; min-width: 320px; width: 100%; position: relative; overflow: hidden; min-height: 100%; display: flex; flex-direction: column; justify-content: flex-start; transition: filter .3s ease-out; background-position: 50% 0%; background-repeat: repeat-y; background-size: 100% auto; background-size: cover; background-attachment: fixed; z-index: 1; }

