.main-menu a:before { content: ''; display: block; height: 2px; left: 0; right: 0; bottom: 0; background: #FFE500; position: absolute; transform: scale(0); transform-origin: 100% 50%; transition: transform .4s ease; }

.main-menu a.current:before { transform: scale(1); }

.web .main-menu a:hover:before { transform: scale(1); transform-origin: 0 50%; }

