.header-socials { margin: .7rem 2% 0 0; }

.bars-mob { width: 4.1rem; height: 4.1rem; border-radius: 50%; background: #fff; display: none; position: relative; cursor: pointer; }

.bars-mob .hamburger { position: absolute; height: 2.4rem; width: 2.2rem; margin-top: 1.4rem; margin-left: .9rem; }

.bars-mob .hamburger span { display: block; width: 100%; height: 2px; position: relative; background: #000; top: 0; left: 0; margin: 0 0 .4rem; transition: .25s ease-in-out; }

.bars-mob .hamburger span:nth-child(1) { transition-delay: .5s; }

.bars-mob .hamburger span:nth-child(2) { transition-delay: .625s; width: 1.4rem; left: .8rem; }

.bars-mob .hamburger span:nth-child(3) { transition-delay: .75s; width: 1.4rem; }

.bars-mob .cross { position: absolute; height: 1.7rem; width: 1.7rem; margin: 1rem 0 0 1rem; transform-origin: center center; transform: rotate(45deg); }

.bars-mob .cross span { display: block; background: #000; transition: .25s ease-in-out; }

.bars-mob .cross span:nth-child(1) { height: 0%; width: 2px; position: absolute; left: 1.1rem; top: 3%; transition-delay: 0s; }

.bars-mob .cross span:nth-child(2) { width: 0%; height: 2px; position: absolute; left: 20%; top: .8rem; transition-delay: .25s; }

.bars-mob.active .hamburger span { width: 0%; }

.bars-mob.active .hamburger span:nth-child(1) { transition-delay: 0s; }

.bars-mob.active .hamburger span:nth-child(2) { transition-delay: .125s; }

.bars-mob.active .hamburger span:nth-child(3) { transition-delay: .25s; }

.bars-mob.active .cross span:nth-child(1) { height: 100%; transition-delay: .625s; }

.bars-mob.active .cross span:nth-child(2) { width: 100%; transition-delay: .375s; }

