.web.content-loaded { overflow: auto; }

.web.content-loaded .section-main__bg { transform: scale(1); transition: transform .8s ease; }

.web.content-loaded .header { transform: translateY(0); opacity: 1; transition: transform .8s ease .3s,  opacity .8s ease .3s; }

.web.content-loaded .main-heading { transform: translateY(0); opacity: 1; transition: transform .8s ease .4s,  opacity .8s ease .4s; }

.web.content-loaded .section-main.scrolled .text--folding-2 .word:before { transform: translateY(0); transition: transform .6s linear; transition-delay: calc(.5s + 30ms * var(--word-index)); }

.web.content-loaded .section-main.scrolled .text--folding-3 .word:before { opacity: 1; transition: opacity .6s linear; transition-delay: calc(.65s + 30ms * var(--word-index)); }

